import  homeListItem from '../../components/home/home-list-item.vue'
import homeInfo from '../../components/home/home-info.vue'
import otherInfo from '../../components/home/other-login.vue'
import homeData from '../../components/home/home-data.vue'
	export default {
		data() {
			return {
				isLogin:true,
				homedata:[
					{name:'糗事',num:0},
					{name:'动态',num:0},
					{name:'评论',num:0},
					{name:'收藏',num:0}
				],
				homeinfo:{
					userpic:'../../static/图片素材/userpic/16.jpg',
					username:'昵称',
					totalnum:0,
					todaynum:0
				},
				list:[
					{icon:'liulan',name:'浏览历史',clicktype:'',url:''},
					{icon:'huiyuanvip',name:'糗事认证',clicktype:'',url:''},
					{icon:'keyboard',name:'审核糗事',clicktype:'',url:''}
				]
			}
		},
		components:{
			homeListItem,
			homeInfo,
			otherInfo,
			homeData
		},
		methods: {
			openlogin(){
				uni.navigateTo({
					url:'../login/login'
				})
			}
		},
		onNavigationBarButtonTap(e){
			if(e.index==0){
				uni.navigateTo({
					url:'../user-set/user-set'
				})
			}
		}
	}
